/* Estilo para el formulario de agregar categorías */
form div {
  margin-bottom: 15px;
}

form input[type="text"],
form input[type="number"],
form select {
  margin-top: 5px;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  background-color: #fafafa;
  outline: none;
}

form button {
  margin-top: 10px;
  padding: 10px 15px;
  font-size: 1rem;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #357abd;
}

button {
  padding: 10px;
  margin-left: 5px;
}

/* Estilo adicional para el nuevo input y botón de categorías */
form div input[type="text"] {
  margin-right: 10px;
  width: calc(100% - 110px); /* Ajuste del ancho para el input */
}

form div button {
  width: 100px; /* Tamaño fijo para el botón Añadir Categoría */
}
